.root{
   font-size: 1.25rem;
}

.title{
   font-weight: 700;
   margin-right: 0.3125rem;
   color: var(--dark-grey);
}
.name{
   transition: all 0.3s ease;

   &:hover{
      color: var(--white);
   }

   &:not(:last-child):after{
      content: ', ';
      color: var(--black);
   }
}